*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Kumbh Sans', sans-serif;
}

.navbar {
  background: #131313;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navbar__container {
  display: flex;
  justify-content: space-between;
  height: 80px;
  z-index: 1;
  width: 100%;
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 50px;
}

#navbar__logo {
  background-color: #87CEEB;
  background-image: linear-gradient(to top, #4169E1 0%, #B0E0E6 100%);
  background-size: 100%;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
  display: flex;
  align-items: center;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
}

.fa-gem {
  margin-right: 0.5rem;
}

.navbar__menu {
  display: flex;
  align-items: center;
  list-style: none;
  text-align: center;
}

.navbar__item {
  height: 80px;
}

.navbar__links {
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  padding: 0 1rem;
  height: 100%;
}

.navbar__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 1rem;
  width: 100%;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  padding: 10px 20px;
  height: 100%;
  width: 100%;
  border: none;
  outline: none;
  border-radius: 4px;
  background: #4169E1;
  color: #fff;
}

.button:hover {
  background: #008B8B;
  transition: all 0.3s ease;
}

.navbar__links:hover {
  color: #4169E1;
  transition: all 0.3s ease;
}

@media screen and (max-width: 960px) {
  .navbar__container {
      display: flex;
      justify-content: space-between;
      height: 80px;
      z-index: 1;
      width: 100%;
      max-width: 1300px;
      padding: 0;
  }

  .navbar__menu {
      display: grid;
      grid-template-columns: auto;
      margin: 0;
      width: 100%;
      position: absolute;
      top: -1000px;
      opacity: 0; 
      transition: all 0.5s ease;
      height: 50vh;
      z-index: -1;
  }

  .navbar__menu.active {
      background: #131313;
      top: 100%;
      opacity: 1;
      transition: all 0.5 ease;
      z-index: 99;
      height: 50vh;
      font-size: 1.6rem;
  }

  #navbar__logo {
      padding-left: 25px;
  }

  .navbar__toggle .bar {
      width: 25px;
      height: 3px;
      margin: 5px auto;
      transition: all 0.3s ease-in-out;
      background: #fff;
  }

  .navbar__item {
      width: 100%;
  }

  .navbar__links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
  }

  #mobile-menu {
      position: absolute;
      top: 20%;
      right: 5%;
      transform: translate(5%, 20%);
  }

  .navbar__btn {
      padding-bottom: 2rem;
  }

  .navbar__icon {
      display: flex;
      padding: 1rem;
      width: 120px;
      margin: 0;
      height: 50vh;
      align-items: center;
  }

  .button {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 80%;
      height: 80px;
      margin: 0;
  }

  .navbar__toggle .bar {
      display: block;
      cursor: pointer;
  }

  #mobile-menu.is-active .bar:nth-child(2) {
      opacity: 0;
  }

  #mobile-menu.is-active .bar:nth-child(1) {
      transform: translateY(8px) rotate(45deg);
  }

  #mobile-menu.is-active .bar:nth-child(3) {
      transform: translateY(-8px) rotate(-45deg);
  }
}

/* Hero Section CSS */

.main {
  background-color: #141414;
}

.main__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-self: center;
  margin: 0 auto;
  height: 90vh;
  background-color: #141414;
  z-index: 1;
  width: 100%;
  max-width: 1300px;
  padding: 0 50px;
}

.main__container .main__content {
  flex: 1;
  display: inline-block;
}


.main__content h1 {
  color: transparent;
  font-size: 4rem;
  /* background-color: #87CEEB;
  background-image: linear-gradient(to top, #4169E1 0%, #33cee2 100%); 
  background-size: 100%;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent; */
  position: relative;
  display: inline-block;
  animation: reveal-text 0.5s ease forwards;
  animation-delay: 1s; 
}

.main__content h1 span {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 0%;
  background-color: #186cda;
  animation: overlay-text 1s ease forwards;
  animation-delay: 0.5s; 
}

h1:nth-child(1) {
  animation-delay: 1s;
}

h1:nth-child(2) {
  animation-delay: 2s;
}

h1:nth-child(4) {
  animation-delay: 3s;
}

h1:nth-child(1) span {
  animation-delay: 0.5s;
}

h1:nth-child(2) span {
  animation-delay: 1.5s;
}

h1:nth-child(4) span {
  animation-delay: 2.5s;
}

.main__content h2 {
  font-size: 4rem;
  background-color: #87CEEB;
  background-image: linear-gradient(to top,#8B008B 0%, #1E90FF 100%);
  background-size: 100%;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
}

.main__content h3 {
  font-size: 4rem;
  background-color: #87CEEB;
  background-image: linear-gradient(to top,#23234e 0%, #20B2AA 100%);
  background-size: 100%;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
}

.main__content p {
  margin-top: 1rem;
  font-size: 2rem;
  font-weight: 700;
  color: #fff;
}

.main__btn {
  font-size: 1rem;
  background-image: linear-gradient(to top, #4169E1 0%, #B0E0E6 100%);
  padding: 14px 32px;
  border: none;
  border-radius: 4px;
  color: #fff;
  margin-top: 2rem;
  cursor: pointer;
  position: relative;
  transition: all 0.35s;
  outline: none;
}

.main__btn a {
  position: relative;
  z-index: 2;
  color: #fff;
  text-decoration: none;
}

.main__btn:after {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: #008B8B;
  transition: all 0.35s;
  border-radius: 4px;
}

.main__btn:hover {
  color: #fff;
}

.main__btn:hover:after {
  width: 100%;
}

.main__img--container {
  display: flex;             
  justify-content: center;   
  align-items: center;       
  box-sizing: border-box;    
  margin: 0 auto;            
  padding: 0;              
  height: 100%;             
  width: 100%;               
}

.main__img {
  height: 10800%;               
  width: 100%;    
  display: flex;
  clip-path: circle();
  justify-content: center;
  object-fit: cover;         
  margin-right: 1px;
  margin-bottom: 5px;
}

.caption {
  margin-top: 5px;
  text-align: center;
  font-size: 35px;
  display: block;
  background-color: #87CEEB;
  background-image: linear-gradient(to top, #4169E1 0%, #33cee2 100%); 
  background-size: 100%;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent; 
  margin-top: 1em;
}

.caption2 {
  text-align: center;
  font-size: 20px;
  display: block;
  align-items: center;
  background-color: #87CEEB;
  background-image: linear-gradient(to top, #4169E1 0%, #33cee2 100%); 
  background-size: 100%;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent; 
}

/* Keyframes */
@keyframes overlay-text {
  50% {
      width: 100%;
      left: 0;
  }
  100% {
      left: 100%;
      width: 0;
  }
}

@keyframes reveal-text {
  100% {
      color: #87CEEB;
      /* background-image: linear-gradient(to top, #4169E1 0%, #33cee2 100%); */
  }
}


/* Mobile Responsive */
@media screen and (max-width: 768px) {
  .main__container {
      display: grid;
      grid-template-columns: auto;
      align-items: center;
      justify-self: center;
      width: 100%;
      margin: 0 auto;
      height: 90vh;
      padding: 0;
  }

  .main__content {
      text-align: center;
      margin-bottom: 4rem;
  }

  .main__content h1 {
      font-size: 2.5rem;
      margin-top: 2rem;
  }

  .main__content h2 {
      font-size: 3rem;
  }

  .main__content p {
      margin-top: 1rem;
      font-size: 1.5rem;
  }
}

@media screen and (max-width: 480px) {
  .main__content h1 {
      font-size: 2rem;
      margin-top: 3rem;
  }

  .main__content h2 {
      font-size: 2rem;
  }

  .main__content p {
      margin-top: 2rem;
      font-size: 1.5rem;
  }

  .main__btn {
      padding: 12px 36px;
      margin: 2.5rem 0;
  }
}

/* Services Section CSS */
.services {
  background: #141414;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* height: 100vh; */
}

.services h1 {
  background-color: #4169E1;
  background-image: linear-gradient(
      to right, 
      #89d8e2 0%,
      #33c7ec 0%,
      #2f77c9 21%,
      #187591 52%, 
      #06375f 78%,
      #186cda 100%
      );
  background-size: 100%;
  margin-bottom: 5rem;
  font-size: 2.5rem;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
  margin-top: 1.5em;
}

.services__container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.services__card {
  margin: 1rem;
  height: 525px;
  width: 400px;
  border-radius: 4px;
  background-image: linear-gradient(
      to bottom,
      rgba(0,0,0,0) 0%,
      rgba(17,17,17,0.6) 100%
      ),
      url('/public/images/projects.JPEG');
  background-size: cover;
  position: relative;
  color: #fff;
  background-position: center;  
  background-repeat: no-repeat;
}

.services__card:nth-child(2){
  background-image: linear-gradient(
      to bottom,
      rgba(0,0,0,0) 0%,
      rgba(17,17,17,0.6) 100%
      ),
      url('/public/images/career.jpg');
      background-size: cover;
      background-position: center;  
      background-repeat: no-repeat;
}

.services__card:nth-child(3){
  background-image: linear-gradient(
      to bottom,
      rgba(0,0,0,0) 0%,
      rgba(17,17,17,0.6) 100%
      ),
      url('/public/images/volunteer.jpg');
      background-size: cover;
      background-position: center;  
      background-repeat: no-repeat;
}

.services h2 {
  position: absolute;
  top: 350px;
  left: 30px;
}

.services__card p {
  position: absolute;
  top: 400px;
  left: 30px;
}

.services__card button {
  color: #fff;
  padding: 10px 20px;
  border: none;
  outline: none;
  border-radius: 4px;
  background: #1E90FF;
  position: absolute;
  top: 440px;
  left: 30px;
  font-size: 1rem;
  cursor: pointer;
}

.services__card:hover {
  transform: scale(1.075);
  transition: 0.2s ease-in;
  cursor: pointer;
}

@media screen and (max-width: 960px) {
  .services {
      height: 100%;
  }

  .services h1 {
      font-size: 2rem;
      margin-top: 12rem;
  }
}

@media screen and (max-width: 480px) {
  .services {
      height: 100%;
  }

  .services h1 {
      font-size: 1.2rem;
  }

  .services__card {
      width: 300px;
  }
}

/* Footer CSS */
.footer__container {
  background-color: #131313;;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: auto;  
  min-height: 100px;  
}

#footer__logo {
  color: #fff;
  display: flex;
  align-items: center;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
}

.footer_links {
  width: 100%;
  max-width: 1000px;
  display: flex;
  justify-content: center;
}

.footer__link--wrapper {
  display: flex;
}

.footer__link--items {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 16px;
  text-align: left;
  width: 160px;
  box-sizing: border-box;
}

.footer__link--items h2 {
  margin-bottom: 16px;
}

.footer__link--items > h2 {
  color: #fff;
}

.footer__link--items a {
  color: #fff;
  text-decoration: none;
  margin-bottom: 0.5rem;
}

.footer__link--items a:hover {
  color: #e9e9e9;
  transition: 0.3s ease-out;
}

/* Contact Icons */
.social__icons--link {
  color: #fff;
  font-size: 24px;
  padding: auto;
  width: 2rem;
  margin: 20px auto 0 auto;
}

.contacts {
  max-width: 1000px;
  width: 100%;
  display: flex;
}

.contacts__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  max-width: 1000px;
  margin: 10px auto 0 auto;
  margin-bottom: 1.5em;
}

.contact__icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 240px;
  color: #fff;
}

.contact__logo {
  color: #fff;
  justify-self: flex-start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}

.website__rights {
  color: #fff;
}

@media screen and (max-width: 820px) {
  .footer_links {
      padding-top: 2rem;
  }

  #footer__logo {
      margin-bottom: 2rem;
  }

  .website__rights {
      margin-bottom: 2rem;
  }

  .footer__link--wrapper {
      flex-direction: column;
  }

  .contacts__wrap {
      flex-direction: column;
  }    
}

@media screen and (max-width: 480px) {
  .footer__link--items {
      margin: 0;
      padding: 10px;
      width: 100%;
  }
  
}